<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="4">
        <div class="grid-content bg-purple dimension-dialog">
          <div class="dimension">
            <div class="item" v-for="item in labelList" :key="item.code" @click="switchTab(item.code)"><span>{{item.name}}</span></div>
          </div>
        </div>
      </el-col>
      <el-col :span="20">
        <div class="container">
          <div class="form" :hidden.sync="formHidden">
            <el-form label-width="180px" :model="queryData" :rules="rules" :inline="true" ref="queryForm">
              <el-form-item label="使用性质" prop="carType">
                <el-select v-model="queryData.usePro" placeholder="请选择使用性质" :multiple="true">
                  <el-option
                    v-for="item in useProList"
                    :key="item.code"
                    :value="item.code"
                    :label="item.name">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="车辆种类" prop="carType">
                <el-select v-model="queryData.carType" placeholder="请选择车辆种类">
                  <el-option
                    v-for="item in carTypeList"
                    :key="item.code"
                    :value="item.code"
                    :label="item.name">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="车辆颜色" prop="carColor">
                <el-input v-model="queryData.carColor" placeholder="请填写车辆颜色"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <div class="option">
              <el-button plain size="small" type="primary" @click="query">查询</el-button>
          </div>
          <div class="data" :hidden.sync="batchDataHidden">
            <div class="title">
              <div class="btn">
                <el-button type="primary" plain size="small" v-show="!editFlag" @click="edit">编辑</el-button>
                <el-button type="primary" plain size="small" v-show="editFlag" @click="save">保存</el-button>
              </div>
            </div>
            <div class="table">
              <base-table
                :columns="batchColumns"
                :data="batchData"
                rowKey="id">
              </base-table>
            </div>
          </div>
          <div class="data" :hidden.sync="singleDataHidden">
            <div class="table">
              <base-table
                :columns="singleColumns"
                :data="singleData"
                rowKey="id"
                @singleEdit="singleEdit">
              </base-table>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-dialog title="编辑" :visible.sync="editDialogVisible" width="35%">
      <div class="dialogForm">
        <el-form label-width="200px" :inline="true" size="small" :model="single">
          <el-form-item label="使用性质:" prop="usePro">
            <el-select style="width:220px" v-model="single.usePro" placeholder="请选择使用性质" :multiple="true">
              <el-option
                v-for="item in useProList"
                :key="item.code"
                :value="item.code"
                :label="item.name">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="车辆种类:" prop="carType">
            <el-select style="width:220px" v-model="single.carType" placeholder="请选择车辆种类">
              <el-option
                v-for="item in carTypeList"
                :key="item.code"
                :value="item.code"
                :label="item.name">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="车辆颜色:" prop="carColor">
            <el-input style="width:220px" v-model="single.carColor" placeholder="请填写车辆颜色"></el-input>
          </el-form-item>
          <el-form-item label="车主姓名:" prop="userName">
            <el-input style="width:220px" v-model="single.userName" placeholder="请填写车主姓名" :disabled='true'></el-input>
          </el-form-item>
          <el-form-item label="购买时间:" prop="buyTime">
            <el-input style="width:220px" v-model="single.buyTime" placeholder="请填写购车时间"></el-input>
          </el-form-item>
          <el-form-item label="是否购买交强险:" prop="isForce">
            <el-select style="width:220px" v-model="single.isForce" placeholder="请选择是否购买交强险">
              <el-option value="yes" label="是"></el-option>
              <el-option value="no" label="否"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div class="option" slot="footer">
        <el-button type="primary" plain size="small" @click="editDialogVisible = false">保存</el-button>
        <el-button type="primary" plain size="small" @click="editDialogVisible = false">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const BaseTable = () => import('@/components/BaseTable')
export default {
  name: 'CommonInfo',
  components: {BaseTable},
  data () {
    return {
      queryData: {
        usePro: [],
        carType: '',
        carColor: ''
      },
      editFlag: false,
      labelList: [
        {code: 'carInfoBatch', name: '投保车辆(批量)'},
        {code: 'carInfoSingle', name: '投保车辆(单条)'},
        {code: 'insProto', name: '投保组合'},
        {code: 'carInsType', name: '车险种类'}
      ],
      rules: {
        usePro: [{required: true, trigger: 'change', message: '查询条件不能为空'}],
        carType: [{required: true, trigger: 'change', message: '查询条件不能为空'}],
        carColor: [{required: true, trigger: 'blur', message: '查询条件不能为空'}]
      },
      useProList: [
        {code: '001', name: '家庭自用'},
        {code: '002', name: '营业出租租赁'},
        {code: '003', name: '营业公路客运'},
        {code: '004', name: '营业城市公交'},
        {code: '005', name: '营业旅游'},
        {code: '006', name: '营业货车'},
        {code: '007', name: '非营业机关'},
        {code: '008', name: '非营业企业'}
      ],
      carTypeList: [
        {code: '001', name: '客车'},
        {code: '002', name: '货车'},
        {code: '003', name: '挂车'}
      ],
      batchColumns: [
        {
          prop: 'usePro',
          label: '使用性质',
          width: 100,
          isEdit: false,
          type: 'select',
          options: [
            {value: '001', label: '家庭自用'},
            {value: '002', label: '营业出租租赁'},
            {value: '003', label: '营业公路客运'},
            {value: '004', label: '营业城市公交'},
            {value: '005', label: '营业旅游'},
            {value: '006', label: '营业货车'},
            {value: '007', label: '非营业机关'},
            {value: '008', label: '非营业企业'}
          ]
        },
        {
          prop: 'carType',
          label: '车辆种类',
          width: 100,
          isEdit: false,
          type: 'select',
          options: [
            {value: '001', label: '客车'},
            {value: '002', label: '货车'},
            {value: '003', label: '挂车'}
          ]
        },
        {
          prop: 'carColor',
          label: '车辆颜色',
          width: 100,
          isEdit: false,
          type: 'input'
        },
        {
          prop: 'userName',
          label: '车主姓名',
          width: 100
        },
        {
          prop: 'buyTime',
          label: '购车时间',
          width: 100,
          isEdit: false,
          type: 'input'
        },
        {
          prop: 'isForce',
          label: '是否购买交强险',
          width: 100,
          isEdit: false,
          type: 'select',
          options: [
            {value: '1', label: '是'},
            {value: '0', label: '否'}
          ]
        }
      ],
      batchData: [
        {id: 1, usePro: '家庭自用', carType: '客车', carColor: '黑色', userName: '张翰', buyTime: '2018/01/01', isForce: '是'},
        {id: 2, usePro: '营业出租租赁', carType: '货车', carColor: '军绿色', userName: '王梅', buyTime: '2016/02/01', isForce: '否'},
        {id: 3, usePro: '营业公路客运', carType: '挂车', carColor: '橙色', userName: '李莉', buyTime: '2017/01/31', isForce: '是'},
        {id: 4, usePro: '营业城市公交', carType: '客车', carColor: '红色', userName: '孙强', buyTime: '2016/02/02', isForce: '否'},
        {id: 5, usePro: '营业旅游', carType: '货车', carColor: '渐变紫', userName: '赵楠', buyTime: '2018/01/16', isForce: '是'},
        {id: 6, usePro: '营业货车', carType: '挂车', carColor: '迷彩色', userName: '钱峰', buyTime: '2017/09/09', isForce: '否'},
        {id: 7, usePro: '非营业机关', carType: '客车', carColor: '银色', userName: '周福', buyTime: '2018/04/02', isForce: '是'},
        {id: 8, usePro: '非营业企业', carType: '货车', carColor: '白色', userName: '郑军', buyTime: '2018/05/01', isForce: '否'}
      ],
      singleColumns: [
        {
          prop: 'usePro',
          label: '使用性质',
          width: 100
        },
        {
          prop: 'carType',
          label: '车辆种类',
          width: 100
        },
        {
          prop: 'carColor',
          label: '车辆颜色',
          width: 100
        },
        {
          prop: 'userName',
          label: '车主姓名',
          width: 100
        },
        {
          prop: 'buyTime',
          label: '购车时间',
          width: 100
        },
        {
          prop: 'isForce',
          label: '是否购买交强险',
          width: 100
        },
        {
          prop: 'operation',
          label: '操作',
          width: 100,
          operation: true,
          operations: [{text: '编辑', event: 'singleEdit'}]
        }
      ],
      singleData: [
        {id: 1, usePro: '家庭自用', carType: '客车', carColor: '黑色', userName: '张翰', buyTime: '2018/01/01', isForce: '是', isEdit: false},
        {id: 2, usePro: '营业出租租赁', carType: '货车', carColor: '军绿色', userName: '王梅', buyTime: '2016/02/01', isForce: '否', isEdit: false},
        {id: 3, usePro: '营业公路客运', carType: '挂车', carColor: '橙色', userName: '李莉', buyTime: '2017/01/31', isForce: '是', isEdit: false},
        {id: 4, usePro: '营业城市公交', carType: '客车', carColor: '红色', userName: '孙强', buyTime: '2016/02/02', isForce: '否', isEdit: false},
        {id: 5, usePro: '营业旅游', carType: '货车', carColor: '渐变紫', userName: '赵楠', buyTime: '2018/01/16', isForce: '是', isEdit: false},
        {id: 6, usePro: '营业货车', carType: '挂车', carColor: '迷彩色', userName: '钱峰', buyTime: '2017/09/09', isForce: '否', isEdit: false},
        {id: 7, usePro: '非营业机关', carType: '客车', carColor: '银色', userName: '周福', buyTime: '2018/04/02', isForce: '是', isEdit: false},
        {id: 8, usePro: '非营业企业', carType: '货车', carColor: '白色', userName: '郑军', buyTime: '2018/05/01', isForce: '否', isEdit: false}
      ],
      formHidden: false,
      batchDataHidden: false,
      singleDataHidden: true,
      editDialogVisible: false,
      single: {
        usePro: ['001'],
        carType: '001',
        carColor: '黑色',
        userName: '张翰',
        buyTime: '2018/01/01',
        isForce: 'yes'
      }
    }
  },
  methods: {
    switchTab (code) {
      if (code === 'carInfoBatch') {
        this.formHidden = false
        this.batchDataHidden = false
        this.singleDataHidden = true
      } else if (code === 'carInfoSingle') {
        this.formHidden = false
        this.batchDataHidden = true
        this.singleDataHidden = false
      } else {
        this.formHidden = true
        this.batchDataHidden = true
        this.singleDataHidden = true
      }
    },
    query () {

    },
    edit () {
      this.batchColumns.forEach(column => {
        if (column.isEdit !== undefined) {
          column.isEdit = true
        }
      })
      this.editFlag = true
    },
    save () {
      this.batchColumns.forEach(column => {
        if (column.isEdit !== undefined) {
          column.isEdit = false
        }
      })
      this.editFlag = false
    },
    singleEdit () {
      this.editDialogVisible = true
    }
  },
  computed: {
    ...mapState({tableHeader: state => state.app.tableHeader})
  }
}
</script>

<style scoped>
  .dimension-dialog > .dimension {
    float: left;
    width: 100%;
    border-bottom: solid 1px #eee;
    border-left: solid 1px #eee;
  }

  .dimension-dialog > .dimension > .item {
    width: 100%;
    height: 47px;
    font-size:12px;
    line-height: 47px;
    border-top: solid 1px #eee;
    border-right: solid 1px #eee;
    cursor: pointer;
    position: relative;
  }
  .dimension-dialog > .dimension > .item:hover{
    background:#F4FAFF;
    font-size:14px;
    border:1px solid #15afff
  }
  .dimension-dialog > .dimension > .item > span {
    padding-left: 10px;
  }
  .dialogForm {
    padding: 20px 10px;
    background-color: #fff;
  }
  .dialogForm > .el-form> .el-form-item{
    margin-bottom: 10px;
  }
</style>



